.container{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 50px 0;
  transform-style: preserve-3d;
  cursor: pointer;
  border-radius: 5px;
  background-color: #afcece;
  transform: scale(1);
  transition: 1s;
  border: 1px solid gray;
}

.container:hover{
  box-shadow: 5px 5px 10px rgba(0, 0, 0, .3);
  transform: scale(1.01);
  transition: 1s;
}

.container .box{
  position: relative;
  width: 20vw;
  height: 30vh;
  transform-style: preserve-3d;
}

.container .box .imgBox{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@keyframes float {
	0% {
    transform: translatey(-20px);
	}
	50% {
		transform: translatey(-40px);
	}
	100% {
		transform: translatey(-20px);
	}
}


.container .box .imgBox img{
  width: 80%;
  height: 80%;
  transform: translateY(0) scaleY(1);
  transition: 1s;
}

.container:hover .box .imgBox img{
  transform: translateY(-40px) scaleY(1);
  transition: 1s;
}

.container .box .contentBox{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 80%;
  left: 10px;
  right: 10px;
  height: 20%;
  background: #fff;
  padding: 10px 15px;
  transform-origin: top;
  transition: .5s;
  transform-style: preserve-3d;
}

.container .box .contentBox h2{  
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.container:hover .box .contentBox{
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  transform: translateZ(50px) scaleY(1);
  color: #fff;
  background: #000;
  transition: 1s;
  border-radius: 5px;
}